.head_second{
    color: red;
    margin-top: 20px;
    font-weight: 750;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;
    gap: 40px;
}

button{
    height: 50px;
    border: none;
    margin-top: 3rem;
    background-color:none;
    font-size: 1.3rem;
    cursor:pointer;
}

.bedroom_section{
    margin-top: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    width: 100svw;
    flex-direction: column;
}

.bedroom_container{
    height: 100%;
    width: 100%;
    background-color: rgb(209, 209, 212);
    display: flex;
    justify-content:center;
    gap: 40px;
    align-items: center;
    flex-wrap: wrap;   
}

.image_bed_item{
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    height: 300px;
    width: 240px;
    background-color: white;

}