*{
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: lighter;
  font-style: normal;
}
.App {
  text-align: center;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.box{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  width: 100%;
  color: white;
  background-color: beige;
  position: fixed;

}

.navigator{
  color: white;
}
.navigator li a{
  color: white;
  display: flex;
  width: 300px;
  justify-content: center;
  align-items: center;
  height: 5vh;
  text-decoration: none;
  color: black;
  font-weight: bolder;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.logo{
  margin: 0px;
  padding: 0px;
  display: felx;
  justify-content: center;
  align-items: center;
  color: black;
  font-weight: bolder;
}
.navigator li{
  list-style-type: none;
  display: flex;
  width: 300px;
  justify-content: center;
  align-items: center;
  align-content: center;

}

.logo{
  display: flex;
  width: 200px;
  justify-content: center;
  align-items: center;
  align-content: center;
}



.container{
  display: flex;
  justify-content:center;
  align-items: center;
  align-content: center;
}


.principle{
  height: 90vh;
  width: 100%;
  margin-top: 20px;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.slider{
  height: 100vh;
  width: 400px;
  /* border: 2px solid black; */
  background-color: red;
}


.icons{
  display: flex;
  width: 250px;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  color: black;
}

.searchbox input{
  margin-top: 15px;
  width: 250px;
  height: 35px;
  left: 80px;
  border:none;
  border-bottom:0.5px solid rgb(62, 60, 60);
}

::-webkit-input-placeholder{
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;
  color: black;
}



.searchbox{
  width: 320px;
  height: inherit;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  margin-left: 9px;
  display: none;
  margin-bottom: 10px;

}

.searchbox button{
  margin-top: 15px;
  height: 35px;
  margin-left: 15px;
  width: 70px;
  border:none;
  background-color: orange;
  color: white;
  font-size: 14px;
  font-weight: 500;
  border-radius: 3px;
}

.searchbox button:hover{
  background-color: rgb(193, 127, 6);
  cursor: pointer;
}

.icons i:hover{
  color: orange;
  cursor: pointer;
  font-size: 22px;
}
.cabinet{
  color: gray;
  font-weight: 900;
}

.title_main{
  color: rgb(5, 4, 4);
  display: flex;
  justify-content: space-between;
  font-weight: 900;
  font-size: 2.5rem;
}

.item{
  margin: 4px;
  height: inherit;
  width: 80%;
  background-color: rgb(195, 194, 194);
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px;
}

.circle{
  height: 400px;
  width: 400px;
  border-radius: 50%;
  background-color: rgb(141, 138, 138);
  display: flex;
  justify-content: center;
  align-items: center;
}

.second_circle{
  height: 90%;
  width: 90%;
  background-color: rgb(124, 123, 123);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.third_circle{
  height: 90%;
  width: 90%;
  background-color: blue;
  display: flex;
  border-radius: 50%;
  background-color: white;

  box-shadow: inset 0px 0px 10px 5px rgba(0, 0, 0, 0.2);
  
}
@media only screen and (max-width: 890px) {
  /* .container {
    flex-direction: column;
    align-items: center;
  } */
  .circle{
    height: 70%;
    width: 45%;
    border-radius: 50%;
  }
  .title_main{
    font-size: 2.5rem;
  }
  .item{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
  }

  .item {
    width: 90%;
  }
  .navigator li a{
    width: 100px;
    font-size: 10px;
  }
  .navigator li{
    display: flex;
    justify-content: center;;
  }

  .box{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

  }
  .icons{
    flex-direction:row-reverse;
  }
  .logo {
    font-size: 1.5rem; /* Adjust font size for smaller screens */
    display: block;
    background-color: orange;
    color: white;
    width: 100%;
  }
  .icons{
    padding-bottom: 10px;
  }

}

@media only screen and (max-width: 576px) {
  .item{
    height: 100%;
    width: 97%;
  }

  .circle{
    height: 150px;
    width: 150px;
    border-radius: 50%;
  }
  .principle{
    margin-bottom: 0px;
    height: 60vh;
  }

  .title_main{
    font-size: 1.5rem;
  }
  .icons{
    padding-bottom: 10px;
  }
  .box{
    position: sticky;
  }
}

